		color: $fxBlue
		padding-right: 15px
	}
	.stockApplySend-bottom {
		display: flex
		flex-direction: row
		padding: 10px
		padding-left: 26px
		.order-checkbox {
			padding-top: 4px
		}
		.selectall-text {
			padding: 6px 0 0 10px
		}
		.send-btn-wrap {
			flex: 1
			text-align: right
			.collect-btn {
				margin-right: 10px
			}
		}
	}
}
</style>
